

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

#home-scroll-wrapper {
  position: absolute;
  width: 100%;
  height: 200%;
  display: block;
}

.letter-k{
  position: absolute;
  top: 25%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 5;
  opacity: 0;
  -webkit-transition: opacity 1s ease-in-out 0s;
  -o-transition: opacity 1s ease-in-out 0s;
  transition: opacity 1s ease-in-out 0s;
}

.letter-k-show{
  
  -webkit-transition: opacity 1s ease-in-out 0s;
  
  -o-transition: opacity 1s ease-in-out 0s;
  
  transition: opacity 1s ease-in-out 0s;
  opacity: 1 !important;
}

#intro {
  position: absolute;
  top: 75%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 5;
  opacity: 0;
  -webkit-transition: opacity 1s ease-in-out 0s;
  -o-transition: opacity 1s ease-in-out 0s;
  transition: opacity 1s ease-in-out 0s;
  color: #F4F4F4;
  font-family: 'Noto Sans', sans-serif;
}

#intro span {
  display: block;
  line-height: 50px;
}

.intro-show {
  -webkit-transition: opacity 1s ease-in-out 0s;
  -o-transition: opacity 1s ease-in-out 0s;
  transition: opacity 1s ease-in-out 0s;
  opacity: 1 !important;
}

#portrait {
  border-radius: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}



#home-content {
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: auto;
}

@-webkit-keyframes logo-left-part-appear {
  from {
    opacity: 0;
    -webkit-transform: translateX(-5%);
            transform: translateX(-5%);
  }

  to {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes logo-left-part-appear {
  from {
    opacity: 0;
    -webkit-transform: translateX(-5%);
            transform: translateX(-5%);
  }

  to {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

#left-part {
  display: block;
  -webkit-transition: all cubic-bezier(0.7, 0.3, 0, 1);
  -o-transition: all cubic-bezier(0.7, 0.3, 0, 1);
  transition: all cubic-bezier(0.7, 0.3, 0, 1);
  /*transform: translateX(-20%);
  -webkit-transform: translateX(-20%);*/
  -webkit-animation: logo-left-part-appear 1s 1;
          animation: logo-left-part-appear 1s 1;
}

#right-part {
  -webkit-transition: all cubic-bezier(0.7, 0.3, 0, 1);
  -o-transition: all cubic-bezier(0.7, 0.3, 0, 1);
  transition: all cubic-bezier(0.7, 0.3, 0, 1);
}

@-webkit-keyframes logo-right-part-reappear {
  from {
    opacity: 0;
    -webkit-transform: translateX(5%);
            transform: translateX(5%);
  }

  to {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes logo-right-part-reappear {
  from {
    opacity: 0;
    -webkit-transform: translateX(5%);
            transform: translateX(5%);
  }

  to {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

.right-part-reappear {
  display: block;
  /*transform: translateX(-20%);
  -webkit-transform: translateX(-20%);*/
  -webkit-animation: logo-right-part-reappear 1s 1;
          animation: logo-right-part-reappear 1s 1;
  /*animation-fill-mode: forwards;*/
}

.content-hide {
  display: none;
}

.content-show {
  display: block;
}

