.flipLower {
  -ms-transform-origin: 50% 0%;
  -webkit-transform-origin: top;
  transform-origin: top center;
  -moz-transform-origin: 0% 50%;
  -webkit-backface-visibility: visible !important;
          backface-visibility: visible !important;
  -webkit-animation: flipLower 8s infinite;
  animation: flipLower 8s infinite;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-transition: all 0.3s cubic-bezier(0.7, 0.3, 0, 1);
  -o-transition: all 0.3s cubic-bezier(0.7, 0.3, 0, 1);
  transition: all 0.3s cubic-bezier(0.7, 0.3, 0, 1);
}

.flipUpper {
  -ms-transform-origin: 50% 100%;
  -webkit-transform-origin: bottom;
  transform-origin: bottom center;
  -moz-transform-origin: 100% 50%;
  -webkit-backface-visibility: visible !important;
          backface-visibility: visible !important;
  -webkit-animation: flipUpper 8s infinite;
  animation: flipUpper 8s infinite;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-transition: all 0.3s cubic-bezier(0.7, 0.3, 0, 1);
  -o-transition: all 0.3s cubic-bezier(0.7, 0.3, 0, 1);
  transition: all 0.3s cubic-bezier(0.7, 0.3, 0, 1);
}

